import React from 'react';
import Intro from '../components/pages/projects/Intro';

const rotate = (hr, mn, sc) => (finalHour, finalMinute, finalSecond) => {
  console.log('finalHour', finalHour);
  const deg = 6;
  let day = new Date();

  let hh = (finalHour || day.getHours()) * 30;
  let mm = (finalMinute || day.getMinutes()) * deg;
  let ss = (finalSecond || day.getSeconds()) * deg;

  hr.style.transform = `rotateZ(${hh + mm / 12}deg)`;
  mn.style.transform = `rotateZ(${mm}deg)`;
  sc.style.transform = `rotateZ(${ss}deg)`;
};

const Clock = ({ staticDate }) => {
  React.useEffect(() => {
    let interval;
    const hr = document.getElementById('hr');
    const mn = document.getElementById('mn');
    const sc = document.getElementById('sc');
    const rotation = rotate(hr, mn, sc);

    if (staticDate) {
      rotation(staticDate?.hr, staticDate?.mn, staticDate?.sc);
    } else {
      interval = setInterval(rotation);
    }
    return () => {
      if (interval) {
        clearInterval(interval);
      }
    };
  }, [staticDate]);

  return (
    <div className='clock'>
      <div className='hour'>
        <div className='hr' id='hr'></div>
      </div>
      <div className='min'>
        <div className='mn' id='mn'></div>
      </div>
      <div className='sec'>
        <div className='sc' id='sc'></div>
      </div>
    </div>
  );
};

const Page404 = () => {
  return (
    <div id='page-404-id'>
      <Clock staticDate={{ hr: 4, mm: 4, sc: 0 }} />
      <Intro header='404' subHeader='description404' />
    </div>
  );
};

export default Page404;
